.complete {
    text-decoration: line-through;
}

body {
    width: 90%;
    margin: auto;
}

.deleteIcon {
    color: black;
}

button.new-todo-button {
    margin-left: 10px;
}

.todo-app, .developer-notes {
    width: 40%;
    display: inline-block;
    vertical-align: top;
}

@media (max-width: 900px) {
    .todo-app, .developer-notes {
        width: 100%;
        display: block;
    }
}